import {FieldDefinition, Valcheck} from 'valcheck/lib/valcheck/Valcheck';
import {BooleanTemplate, TemplateFieldType} from '@linkurious/rest-client';

import {RawFieldChecker} from './RawFieldChecker';

import {InputSerialization, TemplateCheckerAttributes} from './index';

export class BooleanChecker<E> extends RawFieldChecker<E> {
  constructor(check: Valcheck<E>) {
    super(check);
  }

  public get attributes(): TemplateCheckerAttributes {
    return {
      type: TemplateFieldType.BOOLEAN,
      shorthand: 'default',
      defaultSerializer: InputSerialization.BOOLEAN
    };
  }

  /**
   * TemplateField json-options valcheck field definition.
   */
  protected getOptionsFieldDefinition(): FieldDefinition<E> {
    return {
      required: false,
      properties: {
        default: {type: 'boolean'}
      }
    };
  }

  /**
   * Template input value field definition.
   *
   * @param options
   */
  protected getInputFieldDefinition(options?: BooleanTemplate['options']): FieldDefinition<E> {
    return {check: 'boolean'};
  }
}
